<template>
  <div>
    <vue-echarts 
      :option="options" 
      style="width:100%;height:600px;"
    />
  </div>
</template>

<script>
// install한 라이브러리 import
import { VueEcharts } from 'vue3-echarts'
import { reactive, toRefs, onMounted } from 'vue'

export default {
  components : {
    VueEcharts
  },

  setup() {
    const state = reactive({
      options : {}
    })

    onMounted(() => {
      // legend 목록
      const legendList = ['SUCCESS', 'WARNING', 'FAIL']
      // 데이터는 요일 기준으로 random하게 생성
      const xData = ['MON', 'TUE', 'WED', 'THU', 'FRI', 'SAT', 'SUN']
      // 차트 데이터
      const chartData = [
        { data : [120, 132, 142, 170, 180, 230, 250] },
        { data : [125, 140, 150, 160, 220, 280, 350] },
        { data : [140, 162, 160, 177, 180, 215, 220] },
        // { data : [220, 252, 262, 283, 290, 330, 320] },
        // { data : [87, 99, 103, 105, 110, 130, 150] },
        // { data : [90, 97, 112, 120, 130, 133, 131] },
      ]
      // series 데이터 목록
      const series = []
      // legend 기준 series 데이터 생성
      legendList.map((x, i) => {
        series.push({
          name    : x,
          type    : 'bar', // or line
          data    : chartData[i].data
        })
      })

      /**
       * Chart options 정의
       * 
       */
      state.options = {
        // legend 정의
        legend : {
          data  : legendList
        },
        // X축 정의
        xAxis : {
          type : 'category',
          data : xData
        },
        // Y축 정의
        yAxis: {
          type: 'value'
        },
        // bar 유형 데이터 정의
        series: series,

        tooltip : {
            trigger: 'axis'
        }

      }
    })

    return {
      ...toRefs(state),
    }
  }
}
</script>
